import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect
} from "react-router-dom";
import Home from "./Home";
import './styles.css';
import breloom from "./breloom.png";

function App() {
  return (
    <div>
      <img src={breloom} id="homebutton" onclick="window.location.href='https://breloom.xyz';" />
      <Router>
        <div>
          <Switch>
            <Route path="/">
              <Home />
            </Route>
            <Route exact path="/h" render={() => (window.location = "https://hahs.sentral.com.au/portal2/")} />
          </Switch>
        </div>
      </Router>
    </div>
  );
}

export default App;